import React from 'react';
import { Button,
         Card,
         Callout,
         Collapse,
         Classes,
         Colors,
         Divider,
         H5,H4,H3,H2,H1,
         Intent,
         HTMLTable,
         MenuItem, 
         ProgressBar,
         Tag, 
         Tooltip, 
         InputGroup,
         Spinner
    } from '@blueprintjs/core';

import {MultiSelect} from '@blueprintjs/select'

import Technologies from './technologies';

const levelsText = {
    'C2': "Proficient",
    'C1': "Advanced",
    'B2': "Upper-intermediate",
    'B1': "Intermediate",
    'A2': "Pre-intermediate",
    'A1': "Elementary"
};

/*
  Freshness levels:
    0 -> Actual/Current
    1 -> Fresh
    2 -> Old
    3 -> Hobby/Learning
*/

const FreshnessDetail = [
    {title:'Current stack', detail:'Technologies I use in my most recent works and projects.'},
    {title:'Still Fresh stack', detail:'Technologies used in code I maintain or modify occasionally.'},
    {title:'Familiar', detail:'Technologies I used extensively in past projects but I seldom use today.'},
    {title:'Hobby stack',detail: 'Technologies I\'m currently toying with or using in hobby projects.'}
];

const techRowStyle = {verticalAlign: 'middle'};
const getIntent = (val)=>[Intent.DANGER, Intent.WARNING, Intent.SUCCESS][Math.floor(val*3)];

const Header = ({breakPoint}) => 
    <>
    {breakPoint<=2 &&
    <colgroup>
        <col style={{width:breakPoint<-1?"100px":"145px"}}/>
        <col style={{width:"100%"}}/>
        <col style={{width:"160px"}}/>
    </colgroup>
    }
    <thead>
        {breakPoint>=2 ?
        <tr>
            <th>Technology</th>
            <th colSpan="2">Years Active</th>
            <th colSpan="2">Self-assessment</th>
            <th colSpan="2">External assessment</th>
        </tr>
        :
        <tr>
            <th>Technology</th>
            <th colSpan="2">Assessment</th>
        </tr>
        }
    </thead>
    </>;

const ExpRow = ({breakPoint, freshness, name, years, selfAssessment, lvl, externalAssessment, ext_lvl, icon, tags})=>
    breakPoint >=2 ? /* Tablet + size */
    <tr key={name} style={{verticalAlign:'middle'}}>
        <td>
            <img src={icon} alt={name} title={name} width="48px"/>
        </td>

        <td style={techRowStyle} colSpan="2">
            <Tag minimal round>{(years[1]-years[0])||"~1"} year(s)</Tag>
        </td>
        
        <td style={techRowStyle}>
            <Spinner intent={Intent.PRIMARY} size={48} value={selfAssessment}/>
        </td>

        <td style={techRowStyle}><Tag minimal round intent={Intent.PRIMARY}>{levelsText[lvl]}</Tag></td>
        {(externalAssessment && <>
            <td style={techRowStyle}>
                <Spinner intent={getIntent(externalAssessment)} size={48} value={externalAssessment}/>
            </td>
            <td style={techRowStyle}><Tag minimal round intent={Intent.SUCCESS}>Top {100-ext_lvl}%</Tag></td>
            </>)
        || <td style={techRowStyle} colSpan="2"><Tag minimal round>Not Available</Tag></td>}
    </tr>
    : /* Condensed mode */
    <>
    <tr key={name+'-0'} style={{verticalAlign:'middle'}}>
        <td rowSpan={externalAssessment?2:1} style={{textAlign:breakPoint<-1?'center':'left'}}>
            <img src={icon} alt={name} title={name} width="48px" style={{verticalAlign:'middle'}}/>
            <Tag minimal round>{(years[1]-years[0])||"~1"} year(s)</Tag>
        </td>
        { breakPoint>=0 &&
            <td style={techRowStyle} >
                <ProgressBar animate={false} style={{minSize:'25px'}} intent={Intent.PRIMARY} value={selfAssessment}/>
            </td>
        }
        <td colSpan={breakPoint>=0?1:2} style={techRowStyle}>
            <Tag minimal round>{levelsText[lvl]}</Tag>
        </td>
    </tr>
    {externalAssessment &&
    <tr key={name+'-1'} style={{verticalAlign:'middle'}}>
        { breakPoint >= 0 &&
            <td style={techRowStyle}>
                <ProgressBar intent={Intent.SUCCESS} animate={false} value={selfAssessment}/>
            </td>
        }
        <td colSpan={breakPoint>=0?1:2} style={techRowStyle}><Tag minimal round intent={Intent.SUCCESS}>Top {100-ext_lvl}%</Tag></td>
    </tr>
    }
    </>;

const TitleHead = ({breakPoint, title, detail}) => 
    <tr>
        <th style={{backgroundColor: Colors.DARK_GRAY5, color:Colors.WHITE}}
            colSpan="7">
            {title.toUpperCase()}
            {breakPoint>=2 && <small> - {detail}</small>}
        </th>
    </tr>;

class DetailsCallout extends React.Component {
    
    state = {
        isOpen: false
    }

    toggleOpen=()=>this.setState({isOpen: !this.state.isOpen})

    render() {
        let {alwaysOpen, intent, title} = this.props;
        let {isOpen} = this.state;
        return <Callout intent={intent}>
            <H4>{title}{!alwaysOpen && <Button intent={intent} minimal icon={isOpen?"caret-up":"caret-down"} onClick={this.toggleOpen}/>}</H4>
            <Collapse isOpen={ alwaysOpen || isOpen }>
                {this.props.children}
            </Collapse>
        </Callout>
    }
}

export default class SkillSet extends React.Component {
    state = {
        tags:[],
        tagsI:0
    };
    handleTagsUpdate(newTags) {
        this.setState({tags: newTags, tagsI: newTags.reduce((acc,t)=>acc|Technologies.TagsVals[t],0)});
    }
    renderFilterSelectItem = (item, { handleClick, modifiers, query }) => <MenuItem key={item} text={item} onClick={handleClick}/>;
    renderFilterSelectTag = (item) => {return item;};
    handleFilterSelect = (item) => {
        this.handleTagsUpdate([...this.state.tags,item]);
    };
    filterTagPredicate = (query, item) => item.indexOf(query)>-1;
    handleTagRemove = (tag, index) => {
        this.state.tags.splice(index,1);
        this.handleTagsUpdate(this.state.tags);
    };

    technologyFilter = () =>
        <MultiSelect
            items={Technologies.Tags.filter(t=>this.state.tags.indexOf(t)===-1)}
            itemRenderer={this.renderFilterSelectItem}
            tagRenderer={this.renderFilterSelectTag}
            selectedItems={this.state.tags}
            onItemSelect={this.handleFilterSelect}
            itemPredicate={this.filterTagPredicate}
            popoverProps={{ minimal: true }}
            tagInputProps={{ onRemove: this.handleTagRemove }}
        />;

    render() {
        let {breakPoint} = this.props;
        return <div className='bp3-running-text'>
            <Divider vertical="true"/>
            <Card>
                <H3>Technical skills</H3>
                <Callout intent={Intent.PRIMARY} title='Tech stacks come and go'>
                To keep things organized, I'm listing them by how <em>fresh</em> they are in my mind.<br/>
                You can cut down the noise using tags:
                {this.technologyFilter()}
                </Callout>
                <DetailsCallout alwaysOpen={breakPoint>2} intent={Intent.WARNING} title='Beware of the DunningKruger effect'>
                    <a href="https://en.wikipedia.org/wiki/Dunning%E2%80%93Kruger_effect" target="blank">Auto-evaluation is very hard</a>. The following self assessments are what I honestly think 
                    is my current level of expertise.<br/>
                    While not a big fan of random/automated tests, I'll include external assessments provided by <a href="https://app.pluralsight.com/profile/mehdi-abbad" target='blank'>Pluralsight</a> to back up my claims.<br/>
                    Nevertheless, I think that only an <em>objective, capable AND knowledgable</em> peer should be given the right to assess someone's proficiency.
                </DetailsCallout>
            </Card>
            <Divider vertical="true"/>
            <Card style={{padding: breakPoint<2 ? '2px':undefined}}>
                <HTMLTable interactive condensed style={{width:"100%", tableLayout:breakPoint < 2 ? 'fixed' : undefined}}>
                    <Header breakPoint={breakPoint}/>
                    <tbody>
                        {Technologies.Techs
                            .filter(e=>(this.state.tagsI===0 || e.tags&this.state.tagsI))
                            .sort((a,b)=>
                                a.freshness===b.freshness ?
                                    b.selfAssessment - a.selfAssessment
                                    :a.freshness-b.freshness
                            ).reduce((acc, e, i) => {
                                if (e.freshness!==acc.freshness) {
                                    acc.freshness = e.freshness;
                                    acc.push({title_header:true, ...FreshnessDetail[e.freshness]});
                                }
                                acc.push(e);
                                return acc;
                            },[])
                            .map( e => 
                                e.title_header ?
                                    <TitleHead {...e} breakPoint={breakPoint}/>
                                    : <ExpRow {...e} breakPoint={breakPoint}/>
                            )
                        }
                    </tbody>
                </HTMLTable>
            </Card>
        </div>
    }
}