import React from 'react';

import { Card, 
         Classes, 
         Divider,
         MenuItem, 
         Tag, 
         Tooltip, 
         Position,
         Icon } from '@blueprintjs/core';

import { Select } from "@blueprintjs/select";

import { IconNames } from "@blueprintjs/icons";

import moment from 'moment-mini';

const itemText = (item) => item.text;
const selectItemRenderer = (item) => (
  <MenuItem
    key={item.key}
    text={itemText(item)}
  />
);

const heading_titles= [
    {key:0, text:'software engineer'},
    {key:1, text:'full stack developper'},
    {key:2, text:'bug squasher'},
];

const titleSelector = <Select filterable={false} minimal itemRenderer={selectItemRenderer} items={heading_titles}><Tag interactive round minimal>{itemText(heading_titles[0])}</Tag></Select>;

const yearsDiff=(date)=>moment.duration(moment().diff(moment(date)));

const tooltip = (text, content)=> <Tooltip position={Position.TOP} className={Classes.TOOLTIP_INDICATOR} content={content}><em>{text}</em></Tooltip>;



export default ({breakPoint}) => (
  <>
  <Divider vertical="true"/>
  <Card className='bp3-running-text'>
    <h3>Hi, I'm Mehdi, {yearsDiff('1984-12-28').years()}.</h3>
    <p>
    I'm a {titleSelector} by passion with {yearsDiff('2009-02-01').humanize()} of professional experience.<br/>
    I started coding for fun during my middle school years and never got tired of it. Working exclusively in startup environments
    on a variety of different projects and domains, helped me acquire a strong <em>language agnostic</em> analytical intuition.<br/>
    I'm self motivated, solution oriented and treat each problem as an opportunity to learn.
    </p>
    <h4>I believe</h4>
    <p>
    Software engineering is about problem solving, not framework or programming language {tooltip("politics", "Unless of course it's a very badly designed/bloated peace of j...!")}.
    I sincerly think that a good engineer should be flexible enough to adapt to {tooltip("the problem at hand", "Hammers should not be used on screws. Nor should shotguns be used on flies.")}.<br/>
    I'm a strong advocate of KISS, {tooltip("leading by example",<>Like creating a React powered interactive CV <Icon icon="thumbs-up"/></>)}, self documented code and, most importantly, understanding algorithms and logical reasoning {tooltip(" over ", "Function arguments can be googled, resolving a bug in application logic can not.")} remembering language syntax.
    </p>
    <h4>I'm Looking for</h4>
    <p>
    A team that values methodology and creativity. I'm a team player, and not afraid of the grind needed to complete a project on time,
    as long as the management staff values it.
    </p>
  </Card>
  </>);