import React from 'react';

import { Card,
         Callout,
         Classes, 
         Divider,
         H4,
         Icon,
         MenuItem, 
         Position,
         Tag, 
         Tooltip
       } from '@blueprintjs/core';

import { IconNames } from "@blueprintjs/icons";
import marked from 'marked';
import Timeline from "./timeline";
import {VerticalTimeline,VerticalTimelineElement} from "./vertical-timeline";

import Technologies  from './technologies';

const pickTechs = (t) => t.map(e=>Technologies.TechMap[e]);

const mkdn = t => t.map(e=>({
  ...e,
  body : marked(e.body),
  moreLink: e.moreLink && (Array.isArray(e.moreLink)?e.moreLink:[e.moreLink])
}));

const positions = mkdn([
{
  name: 'Founding team member @Ineodev Ltd',
  start: '2009-02-01',
  end: '2018-09-01',
  icon: 'briefcase',
  intent: 'Primary',
  body: 
`I joined Ineodev, an algerian based outsourcing company, after completing my master's degree.

The company serves as product development partner for clients, helping them design, develop and deploy the application they need.

I worked on behalf of Ineodev with clients in different domains and projects. This variety helped me build a strong high level analytical intuition which is an invaluable tool for problem solving.

I also led teams on different occasions, taking full responsabilty for the project completion, laying down the project foundational work (technology stack/frameworks, project structure, code conventions...) as well as mentoring the less experienced team members.

Working on new projects from scratch is always a great opportunity to learn and adopt new technologies and I often spearheaded the adoption of new technologies or methodologies (like version control, moving to Docker etc).
I also frequently assisted my co-workers on their respective tasks and quickly earned the nickname 'bugsquasher' by helping them pinpoint the problem.
`
}, {
  start: '2009-02-01',
  end: '2009-08-15',
  icon: 'briefcase',
  intent: 'Primary',
  url: 'https://www.hanzo.co/',
  name: 'Junior Crawl Engineer @Hanzo Archives',
  image: '',
  body:
`My first assignement at Ineodev was to work with Hanzo, a web archiving start up at a very early stage.
I helped port the inhouse Python based crawler engine to a Linux AWS EC2 environment and learned a lot about TCP/HTTP protocols, SQS/AMQP, NoSql and web crawlers principles.
`
},{
  start: '2003-09-01',
  end: '2008-08-01',
  icon: 'book',
  intent: 'Warning',
  name: "Master's degree in Computer Science",
  body:
`
Completed my MSc in Computer Science at the University of Algiers (USTHB).
Title of my Master's thesis: Design and Implementation of an extention to the XML-DSIG digital signature standard suited for pervasive environments.
`
}
]);

const activities = mkdn([
{
  name: 'CAMA @Aleph Archives Ltd',
  start: '2009-09-15',
  end: '2012-12-15',
  moreLink: {url:'http://ken-webarchiving.com/', text:'CAMA product page'},
  image: require('./assets/projects/CAMA_screen.png'),
  techs: pickTechs(['JavaScript', 'Erlang']),
  body: 
`I was in charge of designing and leading the team developing CAMA, a webarchiving platform that can be tailored to customers' needs.
My mission included:
- Implementing a distributed crawler based on Mozzila and opensource technologies (Xml-XUL, XPCOM, Javascript, RabbitMQ, Erlang) capable of HiFi captures of website content in native format.
- Implementing the browser plugin to access the archived content by implementing proprietary extensions to the HTTP protocol.
- Taking active part in the design of a Scalable Access Server capable of reading efficiently from the (potentialy huge) archived data.

During that time, I gained unvaluable experience in distributed system architecture, TCP/HTTP WEB Browser internals, Security etc.
`
}, { 
  name: 'INALCO @ Industrial Alliance Insurance and Financial Services Inc',
  start: '2010-01-01',
  end: '2010-03-08',
  image: '',
  techs: pickTechs(['JavaScript', 'Erlang', 'ExtJs', 'PostgreSQL']),
  body:
`
IA needed a centralized solution to manage their branches. INALCO was developed using ExtJS and powered by an in house Erlang web application server.

I helped the team finialize the project by:
- Managing, prioritizing and affecting the issues to the other team members.
- Reviewing the code of the junior developers.
- Implementing the more complex part of the application.
`
}, {
  name: 'Mobile Solution @Qubec Loisirs,',
  start: '2012-07-09',
  end: '2012-10-18',
  image: require('./assets/projects/AMD.gif'),
  techs: pickTechs(['JavaScript','Erlang','JQuery','PostgreSQL','TitaniumSDK']),
  moreLink: {url:"http://www.quebecloisirs.com/pages/qui-sommes-nous", text: "Le Club Qubec Loisirs"},
body:`
Qubec Loisirs needed a replacement solution to their aged and error prone "paper-form" based club member recruitment process.
I led the team in charge of developing a mobile solution to help their agents sign in new members more efficiently.

We designed and implemented a complete solution comprising:

- AMD Mobile: A Titanium powered hybrid mobile application deployed on Android tablets that were distributed to agents. Allowing them
to quickly register new members without using paper forms. The application was also designed with minimal mobile PoS features.
- AMD Central: A H-A Erlang based server back-office application helping managers forsee agents activity, with powerful statistics and reporting capabilities.
- Complex bi-directional database synchronisation processes supporting their frequent inventory changes, as well as data exports to their old COBOL accounting system.
`
}, {
  name: 'KEN @Aleph Archives Ltd',
  start: '2013-04-01',
  end: '2013-08-08',
  image: require('./assets/projects/KEN.png'),
  techs: pickTechs(['JavaScript','Erlang','C++','Qt5','NGinX','JQuery']),
  moreLink: {url:'http://ken-webarchiving.com/',text:'KEN product page'},
body:`
CAMA was a great fit for big companies (FINRA Compliance, Web Heritage Management) but smaller and medium companies had different needs.
Aleph Archives wanted a solution that could be easily installed on clients' hosts, while supporting high quality web archiving.
My mission was to:
- Design and implement a cross platform, "scriptable" and light-weight crawler capable of archiving website using the latest web technolgies (HTML5/CSS3) without sacrificing on the archive quality.
- Design and implement a standalone, cross platform archive browser.
- Design and implement a WebUI to access the web archiving platform accessible from both the archive browser and normal browsers.
`
}, {
  name: 'ITS @Algerie Tlcom',
  start: '2013-10-08',
  end: '2015-12-01',
  moreLink: {url:'https://docs.google.com/presentation/d/1hVrE91Zln-l-ZNhrPjjlWER441JVgW74WECimI5J720/edit?usp=sharing', text:'ITS presentation slides'},
  image: require('./assets/projects/ITS_logo.png'),
  techs: pickTechs(['JavaScript','NodeJS','Android SDK','JQuery','PostgreSQL','ExpressJS','NGinX']),
body:`
Algerie Tlcom was phasing out its old copper wire network and replacing it with new fiber optic equipments. The works caused a lot of unreliability issues that their agents weren't able to fix on time.
They needed to optimize their processes by training their agents and develop new tools to streamline the intervention cycle.
They contacted us to develop a mobile application for their field agents with the goal of replacing the traditional inefficient process and allowing them to have a realtime view of the field.

- I designed and implemented the mobile application for Android 4+ with low-end devices in mind.
- I implemented NEAT (Node Express Application Template) an application framework for the server backend. It was subsequently used for other projects at Ineodev.
- I took part in creating the training material as well as presenting the application for the different departments involved.

The server application was based on NodeJS and supported by PostgreSQL (with PostGIS for spatial data) as a backend.
A pilot deployment was conducted on one of the agency which allowed to collect feedbacks from the field agents.
A bigger scale deployment was planned but never conducted due to administration and bureaucratic issues.
`
}, {
  name: 'Embeddable Chat Widget @loquium Social Inc',
  start: '2015-01-01',
  end: '2016-05-01',
  techs: pickTechs(['JavaScript','NodeJS','BackboneJS','JQuery','ExpressJS','PostgreSQL','Erlang','WebRTC', 'Docker','NGinX']),
  moreLink: {url:'http://loquium.com/', text:'loquium website'},
  image: require('./assets/projects/loquium.png'),
body:`
The idea behind loquium is to allow communication between webshops visitors in realtime, creating a social shopping experience.

I was in charge of

- Designing the realtime chat widget and implementing it using websockets.
- Helping in the implementation of the erlang backend.
- Implementing a matching system that filtered the rooms depending on the currently browsed product.
- Thinking of and implementing a "reward" subsystem.
- Prototyping a "call to support" feature using WebRTC.
`
}, {
  name: 'Tachyon @Tenecs Software',
  start: '2015-06-01',
  end: '2016-03-15',
  techs: pickTechs(['Erlang','C']),
  moreLink: {url:'http://www.tenecs.com/products.html', text:'Tachyon Server Page'},
  image: require('./assets/projects/tachyon_logo.png'),
body:`
I was in charge of developing the core component of an ultra high performance web server
exclusively designed for static content targeted for streaming services.

The result was a C based, ultra high performance webserver with minimal memory footprint,
non blocking IO and support for partial requests.

The core component was integrated in a distributed erlang application.
`
}, {
  name: 'OPA 1st phase @OPA Technology',
  start: '2016-05-01',
  end: '2017-05-01',
  techs: pickTechs(['JavaScript','NodeJS','JQuery','BackboneJS','ExpressJS','PostgreSQL','Docker','NGinX']),
  image: require('./assets/projects/OPA_v1.png'),
  moreLink: [{url:'https://www.opatech.ca/projets', text:'OPA Product Page'},
             {url:'https://globalnews.ca/video/3541310/software-to-better-manage-road-construction', text:'Product introduction in Global News Morning'}],
body:`
OPA is a tool targeted at urban planners, public works and transportation technicians that allows them
to manage traffic flow around construction sites and public events.

The project is currently used by the City of Qubec to resolve conflicts among their multiple construction projects.

I was responsible of:

- Developing the MVP with requirements from the city of Qubec.
- Laying down the project architecture as well as the software stack.
- Leading the team implementing the front-end UI.
- Implementing low-level functionalities related to the GIS, as well as the conflict detection mechanism.
- Optimizing database queries by taking full advantage of PostGIS and PostgreSQL.
- Implementing the database migration mechanism.
- Streamlining and automating the deployment process using Docker as a foundation.
`
}, {
  name: 'Vector Tile Generator And Server @OPA Technology',
  start: '2017-07-01',
  end: '2017-08-01',
  techs: pickTechs(['Python2', 'PostgreSQL']),
  image: require('./assets/projects/qgis_layer_slicer.gif'),
body:`
OPA clients needed a way to integrate their own data layers on top of their network layer.

I was assigned the task to develop a solution to generate map tiles
that could easily be served from the database.

- I designed a top-down algorithm suitable for noded network data, and implemented it as a QGis plugin.
- The plugin did the tile segmentation and give output as renormalized SVG tiles that could be loaded in Google Maps.
- The final ouptut was a PostgreSQL COPY statement for fast loading in the database.

I then implemented a tile server into OPA able to compose and restyle the generated tiles on the fly before serving them to the client.
`
}, {
  name: 'OPA 2nd phase @OPA Technology',
  start: '2018-03-01',
  end: '2018-10-01',
  techs: pickTechs(['JavaScript','NodeJS','React','GraphQL','ExpressJS','PostgreSQL','Docker','Relay','NGinX']),
  image: '',
body:`
Phase 2 of OPA is an ongoing effort to rewrite the platform to a newer, more scalable microservice architecure.

I am currently responsible of

- Analyzing and extracting the requirements for the new version based on clients' feedbacks.
- Researching and selecting the most suitable technologies and orchestration tools.
- Designing and implementing the core microservices (auth, users backend, ...).
- Implementing the front end using the latest trends.
`
}
]);

export default (props) => (
  <>
  <Divider vertical/>
  <Card className='bp3-running-text'>
    <Callout title={<><Icon icon="briefcase"/> Work experience / <Icon icon="book"/> Education</>}/>
    <VerticalTimeline>
      {positions.sort((a,b)=>b.end.localeCompare(a.end)).map(a=><VerticalTimelineElement {...a} key={a.name}/>)}
    </VerticalTimeline>
    
    <Callout title={<><Icon icon="projects"/> Past projects and developments</>}/>
    <VerticalTimeline>
      {activities.sort((a,b)=>b.end.localeCompare(a.end)).map(a=><VerticalTimelineElement {...a} key={a.name}/>)}
    </VerticalTimeline>
  </Card>
  </>
);