import React from 'react';
import moment from 'moment-mini';

import {Button, Callout, Card, Classes, Dialog, H6, Icon, Tag} from '@blueprintjs/core';

import "./vertical-timeline.scss";

const VerticalTimeline = ({children}) => (
  <div
    className="vertical-timeline">
    {children}
  </div>
);

const formatDate= (start, end) => {
  let s = moment(start);
  let e = moment(end);
  return `${s.format('MMM YYYY')} - ${e.format('MMM YYYY')} (${moment.duration(e.diff(s)).humanize()})`
}

class VerticalTimelineElement extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false
    };
  }
  
  handleOpen = () => this.setState({isOpen: true});
  handleClose = () => this.setState({isOpen: false});

  render() {
    let {start,
         end,
         moreLink,
         name,
         image,
         body,
         icon,
         intent,
         techs,
         summary} = this.props;

    icon = icon || 'code';
    intent = intent|| 'Success';
    return <div className='vertical-timeline-element'>
      <div>
        <span // eslint-disable-line jsx-a11y/no-static-element-interactions
          className='vertical-timeline-element-icon'
        >
          <Icon icon={icon}/>
        </span>
        <div className='vertical-timeline-element-content'>
          <Callout intent={intent}>
            {image && <img className='vertical-timeline-element-thumbnail' src={image}/>}
            <H6>{name}</H6>
            <small>{formatDate(start,end)}</small>
            <p>{summary}</p>
            <Button text="Tell me more" onClick={this.handleOpen}/>
          </Callout>
          <div className='vertical-timeline-element-content-techs'>
            {techs && techs.map(t=>t && (<Tag key={t.name} minimal small round><img src={t.icon}/> </Tag>))}
          </div>
        </div>
      </div>
      <Dialog
        style={{maxWidth:'90%'}}
        icon={icon}
        onClose={this.handleClose}
        title={name}
        isOpen={this.state.isOpen}>
        <div className={Classes.DIALOG_BODY}>
          <small>{formatDate(start,end)}</small>
          <div dangerouslySetInnerHTML={{__html:body}}/>
          <p>{techs && techs.map(t=>t && (<Tag key={t.name} round>{t.name}</Tag>))}</p>
          {moreLink && moreLink.map(l=><a target="blank" key={l.url} href={l.url}> <Icon icon="link"/> {l.text}</a>)}
        </div>
      </Dialog>
    </div>
  }
}

export {
  VerticalTimeline,
  VerticalTimelineElement
};