import React, { Component } from 'react';

import { Alignment, Card, Divider, Elevation, Icon, Navbar, Switch, Tab, Tabs} from '@blueprintjs/core';
import { IconNames } from "@blueprintjs/icons";

import { ResizeSensor } from "@blueprintjs/core";

import AsciiHeader from './AsciiHeader';
import Summary from './summary';
import Skillset from './skillset';
import Experience from './experience';


import MyPhoto from './assets/me.png';

const breakableTab = ({id, icon, breakPoint, title}) => {
  return  <Tab id={id}>
            <Icon icon={icon}/>{breakPoint>1&& title}
          </Tab>
};


class App extends Component {
	
  constructor(props) {
		super(props);
		this.state={
      navbarTabId: "summary",
      darkTheme: true,
      appWidth: undefined
    };
	}

	handleNavbarTabChange = (TabId) => {
		this.setState({navbarTabId: TabId});
	};

  handleResize = ([entry])=> {
    if (entry.contentRect.width!==this.state.appWidth) {
      this.setState({appWidth:entry.contentRect.width});
    }
  };

  onThemeChange = (e) => {
    this.setState({darkTheme:e.target.checked});
  };

  themeSwitch = ()=><Switch checked={this.state.darkTheme} style={{marginBottom:0}} label={<Icon icon="moon"/>} onChange={this.onThemeChange}/>;

	render() {

    const breakPoint = this.state.appWidth>=760?3:
                        this.state.appWidth>=650?2:
                          this.state.appWidth>=500?1:
                            this.state.appWidth>=425?0:
                              this.state.appWidth>360?-1:-2;

    const headerStyle = {
      backgroundImage: breakPoint>=1 ? `url(${MyPhoto})` : undefined,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center right'
    };

		return (
			<ResizeSensor onResize={this.handleResize}>
        <div className={["App",this.state.darkTheme?'bp3-dark':''].join(' ')}>
          <Card interactive={false} elevation={Elevation.TWO} style={headerStyle}>
            {breakPoint<0 && 
              <div style={{position:'absolute', top:'30px', right:'38px'}}>{this.themeSwitch()}</div>
            }
            <AsciiHeader breakPoint={breakPoint}/>
            <Navbar>
              <Navbar.Group>
                <Navbar.Heading>
                  Software Engineer By <code className="bp3-code">Passion</code>
                </Navbar.Heading>
              </Navbar.Group>
              <Navbar.Group align={Alignment.RIGHT}>
                {breakPoint>0 &&
                  <>
                  <Tabs
                    animate={true}
                    id="navbar"
                    large={true}
                    onChange={this.handleNavbarTabChange}
                    selectedTabId={this.state.navbarTabId}>
                    {breakableTab({id:"summary", icon:IconNames.INFO_SIGN, title:" Me", breakPoint:breakPoint})}
                    {breakableTab({id:"skillset", icon:IconNames.BUILD, title:" Techs", breakPoint:breakPoint})}
                    {breakableTab({id:"experience", icon:IconNames.HISTORY, title:" Experience", breakPoint:breakPoint})}
                  </Tabs>
                  <Navbar.Divider/>
                  </>
                }
                {breakPoint>=0 && 
                  <Navbar.Heading>
                    {this.themeSwitch()}
                  </Navbar.Heading>
                }
              </Navbar.Group>
            </Navbar>
          </Card>
          {(this.state.navbarTabId==='summary' || breakPoint<1)&& <Summary breakPoint={breakPoint}/>}
          {(this.state.navbarTabId==='skillset' || breakPoint<1) && <Skillset breakPoint={breakPoint}/>}
          {(this.state.navbarTabId==='experience' || breakPoint<1) && <Experience breakPoint={breakPoint}/>}
        </div>
      </ResizeSensor>
    );
  }
}

export default App;
